* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background: -webkit-linear-gradient(45deg, #d2bd92, #ddcaa0, #f8f3e0);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.calendar {
  flex-direction: column-reverse;
  align-items: center;
  display: flex;
}

.calendar input {
  max-width: 300px;
  text-align: center;
  color: #fff;
  mix-blend-mode: multiply;
  background: -webkit-linear-gradient(90deg, #c3284d, #f76463);
  border: 0;
  border-radius: 5px;
  font-size: 36px;
  display: table;
  box-shadow: 5px 5px 13px rgba(34, 60, 80, .6);
}

.calendar button {
  width: 100px;
  color: #f5f5f5;
  mix-blend-mode: multiply;
  cursor: pointer;
  background: -webkit-linear-gradient(90deg, #bb056c, #e72d44);
  border: 0;
  border-radius: 4px;
  margin: 10px auto 40px;
  padding: 10px;
  font-size: 18px;
  display: table;
  box-shadow: 5px 5px 13px rgba(34, 60, 80, .6);
}

.timer {
  max-width: 700px;
  border: 10px solid red;
  border-image: -webkit-linear-gradient(90deg, #bb056c, #e72d44) 1;
  justify-content: center;
  margin: 100px auto 0;
  font-size: 72px;
  display: flex;
  box-shadow: 5px 5px 13px rgba(34, 60, 80, .6);
}

.field {
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  display: flex;
  position: relative;
  box-shadow: 5px 5px 13px rgba(34, 60, 80, .6);
}

.label {
  font-size: 18px;
  font-weight: 700;
}

.value {
  min-width: 100px;
  text-align: center;
  color: #fff;
  mix-blend-mode: multiply;
  background: -webkit-linear-gradient(90deg, #c3284d, #f76463);
  border-radius: 5px;
  padding-bottom: 15px;
  display: table;
}

.field:before {
  content: attr(data-title);
  font-size: 14px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.field:not(:last-child):after {
  content: ":";
  position: absolute;
  right: -17px;
}

